import MyTimePicker from '@/components/myTimePicker/MyTimePicker.vue'
import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		MyTimePicker,
		wPicker
	},
	data() {
		return {
			type: '羽毛球',
			time: '',  //选择的时间值
			isshow1: false,  //显示时间选择器
			isshow2: false,  //其他选择器
			defaultValue: ['2小时'],
			hour: '',  //选择的时长
			pickList1: [],
			deadline: '',  //报名截止
			timePickerType: 100,  //100为选择活动时间，200为选择报名截止时间
			venueName: '',
			venueId: '',
			inputNumValue: '',  //人数
			inputPrice1: '',  //钱包支付费用
			inputPrice2: '',  //费用男
			inputPrice3: '',  //费用女
			inputPrice4: '',  //临打费用
			inputBallType: '',  //活动用球
			columns: [],
			optionsList: [],
			selectedIds: [],  //选中的等级
			inputOrganizer: '',  //组织者
			titleValue: '',  //标题
			ruleValue: '',  //活动规则
			isWeek: false,  //是否周活动
			isCancel: true,  //是否可取消
			isUnderLine: true,  //是否接受线下报名
			showMyTextArea: true,
			showMyTextArea2: true,
		}
	},
	onLoad(options) {
		this.columns = [{label:"0.5小时",value:0.5},{label:"1小时",value:1},{label:"1.5小时",value:1.5},{label:"2小时",value:2},{label:"2.5小时",value:2.5},{label:"3小时",value:3},{label:"3.5小时",value:3.5},{label:"4小时",value:4}];
		this.$http.get({
			url: '/v1/rest/public/findDictList',
			data: {
				skey: 'level'
			}
		}).then(resp => {
			// console.log(resp)
			if(resp.status == 200) {
				this.optionsList = resp.data
			}
		})
	},
	onShow() {
		if(uni.getStorageSync('venue')) {
			this.venueName = uni.getStorageSync('venue').name
			this.venueId = uni.getStorageSync('venue').id
			this.titleValue = this.time + this.venueName + '约羽毛球'
		}
	},
	methods: {
		showMyText() {
			this.showMyTextArea = false
		},
		blur1() {
			this.showMyTextArea = true
		},
		showMyText2() {
			this.showMyTextArea2 = false
		},
		blur2() {
			this.showMyTextArea2 = true
		},
		// 选择活动时间
		choiceTime() {
			this.isshow1 = !this.isshow1
			this.timePickerType = 100
		},
		receiveTime(v1, v2){
			this.time = v1
			this.isshow1 = v2
			this.titleValue = this.time + this.venueName + '约羽毛球'
		},
		// 选择活动时长
		choiceDuration() {
			this.isshow2 = true
			this.$refs.picker1.show()
		},
		onConfirm(v) {
			console.log(v)
			this.hour = v.checkArr.value
			this.isshow2 = false
		},
		onCancel() {
			this.isshow2 = false
		},
		// 选择报名截止时间
		choiceDeadline() {
			this.isshow1 = true
			this.timePickerType = 200
		},
		receiveDeadline(v1, v2){
			this.deadline = v1
			this.isshow1 = v2
		},
		// 选择场馆
		choiceVenue() {
			uni.navigateTo({
				url: '/pages/groupOwnerManage/selectVenue/selectVenue'
			})
		},
		// 选择等级
		handleClick(item) {
			let selectedIndex = this.selectedIds.indexOf(item.value)
			 if(selectedIndex >= 0) {
				this.selectedIds.splice(selectedIndex, 1)
			}else{
				this.selectedIds.push(item.value)
			}
			// console.log(this.selectedIds)
		},
		// 输入人数
		inputNum(v) {
			this.inputNumValue = v.detail
		},
		// 输入钱包支付
		InputPrice1(v) {
			this.inputPrice1 = v.detail
		},
		// 输入费用男
		InputPrice2(v) {
			this.inputPrice2 = v.detail
		},
		// 输入人数
		InputPrice3(v) {
			this.inputPrice3 = v.detail
		},
		// 输入人数
		InputPrice4(v) {
			this.inputPrice4 = v.detail
		},
		// 输入标题
		inputTitleValue(v) {
			this.titleValue = v.detail
		},
		// 输入规则
		inputRuleValue(v) {
			this.ruleValue = v.detail
		},
		// 输入用球
		InputBall(v) {
			this.inputBallType = v.detail
		},
		// 输入组织者
		InputOrganizer(v) {
			this.inputOrganizer = v.detail
		},
		// 是否周活动
		switchChange1() {
			this.isWeek = !this.isWeek
		},// 是否可取消
		switchChange1() {
			this.isCancel = !this.isCancel
		},
		// 提交
		submit() {
			if(this.time == '') {
				uni.showToast({
					title: '请选择活动时间！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.hour == '') {
				uni.showToast({
					title: '请选择活动时长！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.hour == '') {
				uni.showToast({
					title: '请选择活动时长！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.deadline == '') {
				uni.showToast({
					title: '请选择报名截止时间！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.venueName == '') {
				uni.showToast({
					title: '请选择活动场馆！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputNumValue == '') {
				uni.showToast({
					title: '请填写活动人数！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputPrice1 == '') {
				uni.showToast({
					title: '请填写钱包支付费用！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputPrice2 == '') {
				uni.showToast({
					title: '请填写男性打球费用！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputPrice3 == '') {
				uni.showToast({
					title: '请填写女性打球费用！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputPrice4 == '') {
				uni.showToast({
					title: '请填写临打费用！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputBallType == '') {
				uni.showToast({
					title: '请填写活动用球！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.selectedIds.length == 0) {
				uni.showToast({
					title: '请选择等级要求！',
					duration: 2000,
					icon: 'none'
				}); 
			}else if(this.inputOrganizer == 0) {
				uni.showToast({
					title: '请填写组织者！',
					duration: 2000,
					icon: 'none'
				}); 
			}else{
				const params = {
					userId: uni.getStorageSync('userInfo').userId,
					type: 'sportsKinds_01',
					timeStart: this.time,
					duration: this.hour,
					endTime: this.deadline,
					venueId: this.venueId,
					people: this.inputNumValue,
					walletPayMoney: this.inputPrice1,
					moneyMan: this.inputPrice2,
					moneyWomen: this.inputPrice3,
					temporaryMoney: this.inputPrice4,
					ballType: this.inputBallType,
					occupationLevel: this.selectedIds.join(','),
					title: this.titleValue,
					content: this.ruleValue,
					isLower:this.isUnderLine,
					isWeek: this.isWeek,
					isCancel: this.isCancel
				}
				// console.log(params)
				this.$http.post({
					url: '/v1/rest/manage/releaseActivities',
					data: params
				}).then(resp => {
					// console.log(resp)
					if(resp.status == 200) {
						uni.showToast({
							title: '发布成功！',
							duration: 2500,
							icon: 'none'
						})
					}
				})
				uni.removeStorageSync('venue')
			}
		}
	}
}
